.cover-photo {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 1;
  }

.profile-page .page-header {
    height: 380px;
    background-position:center;
}

.page-header {
    background-size: cover;
    margin: 0;
    padding: 0;
    border: 0;
    display: flex;
    align-items: center;
}

.header-filter::before {
    background: rgba(0,0,0,.5);
}

.main-raised {
    border-radius: 8px;
    box-shadow: 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12), 0 8px 10px -5px rgba(0,0,0,.2);
}

.main {
    width: 1000px;
    height: 163px;
    top: -50px;
    left: 170px;
    right: 183px;
    background: #FFF;
    position: relative;
    z-index: 3;
}

.profile-page .profile {
    text-align: left;
}

.profile-page .profile img {
    max-width: 160px;
    width: 100%;
    margin: 0 auto;
    transform: translate3d(0,-50%,0);
}

.img-raised {
    box-shadow: 0 5px 15px -8px rgba(0,0,0,.24), 0 8px 10px -5px rgba(0,0,0,.2);
}

.img-fluid, .img-thumbnail {
    position: absolute;
    max-width: 100%;
    height: 152px;
    width: 152px;
    top: 3.7px !important;
    left: 24px !important;
    bottom: 2.72%;
    border-radius: 8px;
}


/* .description */
.profile-page .description {
    margin: 10 20 0 10px;
    max-width: 600px;
    font-size: 13px;
    color: rgb(22, 21, 21);
    font-weight: 300;
    margin-left: 185px;
} 

