.twPc-button {
    margin: -15px 900px 0;
    text-align: right;
    float:left !important
}

.twPc-divName {
    margin: 5px 170px 2px;
    font-size: 18px;
    font-weight: 700;
    text-align: left;
}
.twPc-divName a {
    color: inherit !important;
}

.twPc-Arrange {
    display: table;
}

ul.twPc-Arrange {
    list-style: outside none none;
    margin: -15px;
    padding: -5px;
}
.twPc-ArrangeSizeFit {
    display: table-cell;
    padding: 20px;
}
.twPc-ArrangeSizeFit a:hover {
    text-decoration: none;
}
.twPc-StatValue {
    color: black;
}
.twPc-StatLabel {
    color: #8899a6;
    font-size: 15px;
}


